import React from "react";
import { LeftScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";
import Constants from "../Constants";
import RespondersVideo from "./RespondersVideoCard";
import RespondersSurvey from "./RespondersSurveyCard";
import BAUCCTV from "./BAU-CCTV";
import FloodCCTV from "./Flood-CCTV";
function LeftScreen() {
  const [leftScreenState, setLeftScreenState] = useRecoilState(LeftScreenState);
  const location = useLocation();
  return (
    <div className={"left-screen-container"}>
      <div className={"video-screen-container"}>
        {location.pathname !== "/citizen-watch" && (
          <div>
            {leftScreenState == Constants.videoType.BAUCCTV && <BAUCCTV />}
            {leftScreenState == Constants.videoType.floodCCTV && <FloodCCTV />}
          </div>
        )}
        {location.pathname === "/citizen-watch" && (
          <div>
            {leftScreenState == Constants.videoType.responderVideo && (
              <RespondersVideo />
            )}
            {leftScreenState == Constants.videoType.surveyVideo && (
              <RespondersSurvey />
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default LeftScreen;
