import React from "react";
import { RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";
import Constants from "../Constants";
import RapidDeployCCTV from "./RapidDeployCCTV";
import Iframe from "react-iframe";

function RightScreen() {
  const [rightScreenState, setRightScreenState] =
    useRecoilState(RightScreenState);
  const location = useLocation();
  return (
    <div className={"right-screen-container"}>
      <div className={""}>
        {location.pathname !== "/citizen-watch" && (
          <div>
            {rightScreenState == Constants.videoType.BAUBrandwatch && (
              <div className={"brand-watch-bau"} />
            )}
            {rightScreenState == Constants.videoType.floodBrandWatch && (
              <div className={"brand-watch-flood"} />
            )}
            {rightScreenState == Constants.videoType.rapidDeploy && (
              <RapidDeployCCTV />
            )}
          </div>
        )}
        {rightScreenState == Constants.videoType.responderVideo && (
          <Iframe
            src="https://www.google.com/maps/embed?pb=!4v1631095211520!6m8!1m7!1s2QlxSXlv-ovB_NthunkDWA!2m2!1d1.414412064989025!2d103.8332444921412!3f257.7082839417502!4f-4.404739181216783!5f0.7820865974627469"
            width="1920"
            height="1080"
            style="border:0;"
            allowfullscreen=""
            loading="lazy"
          ></Iframe>
        )}
      </div>
    </div>
  );
}

export default RightScreen;
